export default {
	data() {
		return {
			account: '',
			phone:'',
			code:'',
			realcode:'',
			isSend:false,
			seconds:30
		}
	},
	onLoad() {
		
	},
	methods: {
		getCode(){
			if(!this.account){
				uni.showToast({
					icon: 'none',
					title: '请填写会员账号或手机号',
				});
				this.account = '';
				return;
			}
			if(!this.customMethods.isPoneAvailable(this.phone)){
				uni.showToast({
					icon: 'none',
					title: '手机号格式不正确，请重新输入',
				});
				this.phone = '';
				return;
			}
			var data = {
				'mobile':this.phone,
				'smstype':3,
				'userName':this.account
			}
			this.isSend = true;
			this.cutDown();
			this.sendCode(data);
			
		},
		sendCode(data){
			uni.request({
				url:this.websiteUrl + 'getcaptcha',
				method:'POST',
				data:data,
				dataType:'json',//服务器返回json格式数据
				header:{'Content-Type':'application/json','Token':''},
				success:res=>{
					console.log("发送验证码成功："+JSON.stringify(res))
					var data = res.data;
					//服务器返回响应，根据响应结果，分析是否登录成功；
					var that = this;
					if(data.code == 200){
						uni.showToast({
							icon: 'success',
							title: '验证码发送成功',
							duration:1000
						});
						this.realcode = data.data.code;
					}else {
						uni.showToast({
							icon: 'none',
							title: data.message,
						});
					}
				},
				fail: () => {},
				complete: () => {}
			})
		},
		cutDown(){
			setInterval(()=>{
				var second = this.seconds--;
				if(second ==0){
					this.isSend = false;
					this.seconds = 30;
				}
			},1000)
		},
		toSetPassword(){
			console.log("点击下一步，跳到修改密码页面");
			var that = this;
			var code = Number(this.code);
			var realcode = this.realcode;
			console.log(typeof code);
			console.log(typeof realcode)
			if(!this.code) {
				uni.showToast({
					icon: 'none',
					title: '请输入验证码',
				});
				return;
			}else if(Number(this.code) != Number(this.realcode)){
				uni.showToast({
					icon: 'none',
					title: '验证码不正确',
				});
				return;
			}
			uni.navigateTo({
				url:"/pages/login/setpassword/setpassword?userName="+this.account+'&mobile='+this.mobile+'&pwd='+this.realcode
			})
			
		}
	}
}
